import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
System.propTypes = {
  discount: PropTypes.object,
};
export default function System({ discount }) {
  const { enqueueSnackbar } = useSnackbar();
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
    app_name: Yup.string().required(),
    app_phone: Yup.string().required(),
    app_address: Yup.string().required(),
    app_email: Yup.string().required().email(),
    facebook: Yup.string().required(),
    tiktok: Yup.string().required(),
    instagram: Yup.string().required(),
    playstore_url: Yup.string().nullable(),
    appstore_url: Yup.string().nullable(),
    update_title: Yup.string().nullable(),
    update_desc: Yup.string().nullable()
  });

  const defaultValues = useMemo(
    () => ({
      app_name: discount?.app_name,
      app_phone: discount?.app_phone,
      app_address: discount?.app_address,
      app_email: discount?.app_email,
      facebook: discount?.facebook,
      instagram: discount?.instagram,
      tiktok: discount?.tiktok,
      playstore_url: discount?.playstore_url,
      appstore_url: discount?.appstore_url,
      update_title: discount?.app_update_title,
      update_desc: discount?.app_update_desc
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/systeminfo/${AccessToken}/habukhan/secure`, data)
      enqueueSnackbar('Updated success!');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFTextField name="app_name" label="SYSTEM NAME" />
              <RHFTextField name="app_phone" label="SYSTEM PHONE NUMBER" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="app_email" label="SYSTEM EMAIL" type="email" />
              <RHFTextField name="app_address" label="SYSTEM ADDRESS" />
              <RHFTextField name="facebook" label="SYSTEM FACEBOOK URL" />
              <RHFTextField name="instagram" label="SYSTEM INSTAGRAM URL" />
              <RHFTextField name="tiktok" label="SYSTEM TIKTOK URL" />
              <RHFTextField name="playstore_url" label="PLAY STORE URL" />
              <RHFTextField name="appstore_url" label="APP STORE URL" />
              <RHFTextField name="update_title" label="APP UPDATE TITLE" />
              <RHFTextField name="update_desc" label="APP UPDATE DESCRIPTION" multiline rows={3} />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
